const gray =  getComputedStyle(document.documentElement).getPropertyValue('--color-gray');
const physicalGreen = getComputedStyle(document.documentElement).getPropertyValue('--color-physical-green');
const kindleBlue = getComputedStyle(document.documentElement).getPropertyValue('--color-kindle-blue');
const audibleOrange = getComputedStyle(document.documentElement).getPropertyValue('--color-audible-orange');

export { gray, physicalGreen, kindleBlue, audibleOrange };