import { physicalGreen, audibleOrange, kindleBlue } from './colors.js';

// Possible formats we can deliver books in.
const BookFormats = {
  PHYSICAL: 'Physical copy',
  KINDLE: 'Kindle',
  AUDIBLE: 'Audible',
};

function colorForFormat(format) {
  switch (format) {
    case BookFormats.PHYSICAL:
      return physicalGreen;
    case BookFormats.AUDIBLE:
      return audibleOrange;
    case BookFormats.KINDLE:
      return kindleBlue;
  }
}

function isMobile() {
  return window.matchMedia("(max-width: 800px)").matches;
}

function onClickGoToId(event, id) {
  event.preventDefault();
  document.getElementById(id).scrollIntoView();

  if (isMobile()) {
    // Now the element is at the top of the page, so the basket covers it. In order to get the end of the basket to be right about the element (as desired)
    // we scroll up by the basket height.
    const basketHeightinRems = parseFloat(getComputedStyle(document.documentElement).getPropertyValue('--basket-height').replace('rem', '')) + 3; // empirical, TODO
    const convertRemToPixels = (rem) => rem * parseFloat(getComputedStyle(document.documentElement).fontSize);
    const basketHeightinPixels = convertRemToPixels(basketHeightinRems);
    window.scrollBy(0, -basketHeightinPixels);
  }
}

export { BookFormats, colorForFormat, onClickGoToId };