import React from 'react';
import PropTypes from 'prop-types';
import './BookPanel.scss';
import { BookFormats } from './utils.js';

import FancySeparator from './FancySeparator';

export default class BookPanel extends React.Component {
  static propTypes = {
    book: PropTypes.object.isRequired,

    formatSelected: PropTypes.string,
    onFormatSelected: PropTypes.func.isRequired,
  };

  render() {
    return (
      <div className="book-panel">
        <div style={{ display: 'flex' }} className="cover-and-quote-container">
          <div className="cover-container">
            <img className="cover" src={this.props.book.coverImage}/>
          </div>

          <div className="pull-quote-and-summary-container">
            {this.renderPullQuoteIfNecessary()}

            <div className="summary-container">
              {this.renderSummary(this.props.book.summaryParagraphs)}
            </div>
          </div>
        </div>

        {this.renderExcerptSectionIfNecessary()}
        {this.renderQuotesSectionIfNecessary()}

        {this.renderChooseFormatSection()}
      </div>
    );
  }

  renderSummary(summaryParagraphs) {
    // Some of the summary text contains cosmetic HTML tags. It's safe to set inner HTML here because we control the text.
    const ps = summaryParagraphs.map(
      (text, i) => <p key={i} dangerouslySetInnerHTML={{__html: text}}></p>
    );

    return (
      <div>
        {ps}
      </div>
    );
  }

  renderQuotesSectionIfNecessary() {
    if (!this.props.book.quotes) {
      return null;
    }

    return (
      <div>
        <FancySeparator/>

        <h2 style={{ textAlign: 'center', marginTop: '2rem' }}>Quotes</h2>
        {this.props.book.quotes.map( ({ text, byline }) => this.renderQuote(text, byline) )}
      </div>
    );
  }

  renderPullQuoteIfNecessary(fontSize='20pt') {
    if (!this.props.book.pullQuote) {
      return null;
    }

    return (
      <div style={{ marginLeft: 'auto', marginRight: 0 }}>
        {/* Sometimes this text contains cosmetic HTML tags. It's safe to set inner HTML here because we control the text. */}
        <div style={{fontSize: fontSize, fontWeight: 'bold'}} dangerouslySetInnerHTML={{ __html: `“${this.props.book.pullQuote.text}”`}}>
        </div>

        {/* Sometimes this text contains cosmetic HTML tags. It's safe to set inner HTML here because we control the text. */}
        <div style={{marginTop: '1rem', textAlign: 'right', marginLeft: 'auto', maxWidth: '20rem'}} dangerouslySetInnerHTML={{ __html: `— ${this.props.book.pullQuote.byline}`}}>
        </div>
      </div>
    );
  }

  renderQuote(text, byline) {
    return (
      <div key={text} style={{ marginTop: '2rem' }}>
        {/* Sometimes this text contains cosmetic HTML tags. It's safe to set inner HTML here because we control the text. */}
        <div dangerouslySetInnerHTML={{ __html: `“${text}”`}}>
        </div>

        {/* Sometimes this text contains cosmetic HTML tags. It's safe to set inner HTML here because we control the text. */}
        <div style={{ marginTop: '1rem', textAlign: 'right', marginLeft: 'auto'}} dangerouslySetInnerHTML={{ __html: `— ${byline}`} }>
        </div>
      </div>
    );
  }

  renderExcerptSectionIfNecessary() {
    if (!this.props.book.excerpt) {
      return null;
    }

    return (
      <div>
        <FancySeparator/>

        <h2 style={{ textAlign: 'center', marginTop: '2rem'}}>Excerpt</h2>
        <div>
          {/* It's safe to set inner HTML here because we control the text. */}
          <div dangerouslySetInnerHTML={{ __html: `${this.props.book.excerpt}`}}>
          </div>
        </div>
      </div>
    );
  }

  renderChooseFormatSection() {
    const isFormatAvailable = (format) => this.props.book.availableFormats.includes(format);
    const classNamesForFormat = (format) => `${this.props.formatSelected === format ? 'selected' : ''} ${this.props.book.availableFormats.includes(format) ? '' : 'unavailable'}`;

    return (
      <div style={{ marginTop: '5rem' }}>
        <div style={{ textAlign: 'center', marginBottom: 0 }}>
          How do you want to receive your free copy of <b>{this.props.book.name}</b>?
        </div>

        <div style={{ margin: 'auto' }}>
          <div
            className={`select-format-button physical ${classNamesForFormat(BookFormats.PHYSICAL)}`}
            onClick={isFormatAvailable(BookFormats.PHYSICAL) ? (() => this.props.onFormatSelected(BookFormats.PHYSICAL)) : null}
          >
            <span>Physical copy</span>
            <br/>
            <span style={{ fontSize: '10pt' }}>shipped to your house</span>
            <span>{isFormatAvailable(BookFormats.PHYSICAL) ? '' : 'unavailable'}</span>
          </div>
          
          <div
            className={`select-format-button kindle ${classNamesForFormat(BookFormats.KINDLE)}`}
            onClick={isFormatAvailable(BookFormats.KINDLE) ? (() => this.props.onFormatSelected(BookFormats.KINDLE)) : null}
          >
            <span>Kindle ebook {isFormatAvailable(BookFormats.KINDLE) ? '' : 'unavailable'}</span>
          </div>

          <div
            className={`select-format-button audible ${classNamesForFormat(BookFormats.AUDIBLE)}`}
            onClick={isFormatAvailable(BookFormats.AUDIBLE) ? (() => this.props.onFormatSelected(BookFormats.AUDIBLE)) : null}
          >
            <span>Audible audiobook {isFormatAvailable(BookFormats.AUDIBLE) ? '' : 'unavailable'}</span>
          </div>
        </div>
      </div>
    );
  }
}