const MapAndTerritory = {
  name: 'Map And Territory',
  author: 'Eliezer Yudkowsky',
  coverImage: mapAndTerritoryCover,

  summaryParagraphs: [
    "If you live in an urban area, you probably don’t need to walk very far to find a martial arts dojo. Why aren’t there dojos that teach rationality? Very recently — in just the last few decades — the human species has acquired a great deal of new knowledge about human rationality. Experimental investigations of empirical human psychology; and theoretical probability theory to interpret what our experiments tell us; and evolutionary theory to explain the conclusions. These fields give us new focusing lenses through which to view the landscape of our own minds. We have a shared vocabulary in which to describe problems and solutions.",
    "Humanity may finally be ready to synthesize the martial art of mind: to refine, share, systematize, and pass on techniques of personal rationality. When human brains try to do things, they can run into some very strange problems. Self-deception, confirmation bias, magical thinking — it sometimes seems our ingenuity is boundless when it comes to shooting ourselves in the foot. In <i>Map and Territory</i>, decision theorist Eliezer Yudkowsky asks what a “martial art” of rationality would look like, beginning with the basic fighting stance — the orientation toward the world that lets us get the most bang for our cognitive buck, that best positions us to understand and react to brains’ strange acts of self-destruction.",
    "<i>Description from <a href='https://www.amazon.com/Map-Territory-Rationality-AI-Zombies/dp/1939311233' target='_blank' rel='noopener noreferrer'>Amazon</a>.</i>",
  ],

  excerpt: `
    <p>Once upon a time, there was an instructor who taught physics students. One day the instructor called them into the classroom and showed them a wide, square plate of metal, next to a hot radiator. The students each put their hand on the plate and found the side next to the radiator cool, and the distant side warm. And the instructor said, <i>Why do you think this happens?</i> Some students guessed convection of air currents, and others guessed strange metals in the plate. They devised many creative explanations, none stooping so low as to say “I don’t know” or “This seems impossible.”</p>

    <p>And the answer was that before the students entered the room, the instructor turned the plate around.</p>

    <p>Consider the student who frantically stammers, “Eh, maybe because of the heat conduction and so?” I ask: Is this answer a proper belief? The words are easily enough professed—said in a loud, emphatic voice. But do the words actually control anticipation?</p>

    <p>Ponder that innocent little phrase, “because of,” which comes before “heat conduction.” Ponder some of the <i>other</i> things we could put after it. We could say, for example, “Because of phlogiston,” or “Because of magic.”</p>

    <p>“Magic!” you cry. “That’s not a <i>scientific</i> explanation!” Indeed, the phrases “because of heat conduction” and “because of magic” are readily recognized as belonging to different <i>literary genres</i>. “Heat conduction” is something that Spock might say on <i>Star Trek</i>, whereas “magic” would be said by Giles in <i>Buffy the Vampire Slayer</i>.</p>

    <p>However, as Bayesians, we take no notice of literary genres. For us, the substance of a model is the control it exerts on anticipation. If you say “heat conduction,” what experience does that lead you to <i>anticipate</i>? Under normal circumstances, it leads you to anticipate that, if you put your hand on the side of the plate near the radiator, that side will feel warmer than the opposite side. If “because of heat conduction” can also explain the radiator-adjacent side feeling <i>cooler</i>, then it can explain pretty much anything.</p>
  `,

  availableFormats: [
    BookFormats.PHYSICAL,
    BookFormats.AUDIBLE,
    BookFormats.KINDLE,
  ],
};

