import React from 'react';
import PropTypes from 'prop-types';
import './App.scss';

import Links from './Links';
import DetailsModal from './DetailsModal';
import Button from './Button';
import BookPanel from './BookPanel';
import Banner from './Banner';
import SelectABook from './SelectABook';
import FancySeparator from './FancySeparator';
import { BookFormats, onClickGoToId } from './utils.js';

export default class App extends React.Component {
  static propTypes = {
    defaultBook: PropTypes.object,
  };

  constructor(props) {
    super(props);

    // If a default book is provided, we start with it in our basket. The default format is physical.
    let defaultBookWithFormat;
    if (this.props.defaultBook) {
      defaultBookWithFormat = { ...this.props.defaultBook, format: BookFormats.PHYSICAL };
    }

    this.state = {
      // You can choose up to two books.
      leftBook: defaultBookWithFormat,
      rightBook: undefined,

      // Tracks whether we're currently displaying just the default book.
      // If a default book is specified, we want the first thing the user sees to be a page about that.
      displayingJustDefaultBook: Boolean(this.props.defaultBook),

      // Which book are we displaying book details for?
      displayingBookDetailsFor: undefined,
    };
  }

  render() {
    let banner, spacingDivForBanner, content;
    if (this.state.displayingJustDefaultBook) {
      content = (
        <div className="book-panel-container">
          <BookPanel
            book={this.props.defaultBook}
            formatSelected={this.state.leftBook.format}
            onFormatSelected={(format) => this.setState({ leftBook: { ...this.state.leftBook, format } })}
          />

          <div style={{ marginTop: '3rem', display: 'flex' }}>
            <Button
              primaryColor='black'
              secondaryColor='white'
              flipTextColor={true}
              extraSpanStyles={{ width: '100%', height: '5rem' }}
              href={this.checkoutUrl()}
            >
              <span>
                <span>Get Just This Book</span>
              </span>
            </Button>

            <Button
              primaryColor='black'
              secondaryColor='white'
              flipTextColor={true}
              extraSpanStyles={{ width: '100%', marginLeft: '1rem', height: '5rem' }}
              onClick={this.onClickGetAnotherBook.bind(this)}
            >
              Choose An Additional Book
            </Button>
          </div>
        </div>
      );
    } else {
      content = (
        <SelectABook
          booksSelected={[this.state.leftBook, this.state.rightBook]}
          onBookAdded={this.onBookAdded.bind(this)}
          onBookRemoved={this.onBookRemoved.bind(this)}
          startDisplayingBookDetailsFor={this.startDisplayingBookDetailsFor.bind(this)}
        />
      );
    }

    let bookDetailsModal, topInfo;
    if (this.state.displayingBookDetailsFor) {
      bookDetailsModal = (
        <DetailsModal
          book={this.state.displayingBookDetailsFor}
          onBookAdded={this.onBookAdded.bind(this)}
          onBookRemoved={this.onBookRemoved.bind(this)}
          closeModal={this.stopDisplayingBookDetails.bind(this)}
        />
      );
    } else if (!this.state.displayingJustDefaultBook) {
      banner = (
        <Banner
          leftBook={this.state.leftBook}
          rightBook={this.state.rightBook}
          onBookRemoved={this.onBookRemoved.bind(this)}
          checkoutUrl={this.checkoutUrl()}
          startDisplayingBookDetailsFor={this.startDisplayingBookDetailsFor.bind(this)}
        />
      );
      spacingDivForBanner = <div className="spacing-div-for-banner"></div>;
      topInfo = (
        <div style={{ maxWidth: '50%', minWidth: '20rem', margin: '0 auto' }}>
          <p>Choose up to 2 books below to receive free copies of. We can send you a physical copy, a Kindle ebook, or an Audible audiobook.</p>
        </div>
      );
    }

    return (
      <div>
        {banner}

        <div className="app">
          {spacingDivForBanner}

          <div style={{ paddingTop: '2rem', marginBottom: '4rem' }}>
            <div style={{textAlign: 'center', marginBottom: '3rem'}}>
              <h1 className="title">
                Horizon Bookshelf
              </h1>

              {topInfo}

              <div className="navbar" style={{ fontVariant: 'small-caps', marginTop: '1rem' }}>
                <a href="" onClick={(event) => onClickGoToId(event, 'about')}>
                  About
                </a>

                <span style={{ marginLeft: '0.5rem', marginRight: '0.5rem'}}>
                  •
                </span>

                <a href="" onClick={(event) => onClickGoToId(event, 'links')}>
                  Further Reading
                </a>
              </div>
            </div>
          </div>

          {content}
          {bookDetailsModal}

          <FancySeparator/>

          <div id='about' style={{padding: '2rem'}}>
            <h2>What is Horizon Bookshelf?</h2>
            <div>
              <p>
                <i>What will be the most important problems of the next 50 years? If we think we've found one, how likely are we to be mistaken? Which problems — and which moral patients — are being overlooked? Which global trends will continue, and which will break?</i>
              </p>

              <p>
                Horizon Bookshelf was created by <a href="https://www.openphilanthropy.org/" target="_blank" rel="noreferrer">Open Philanthropy</a>, a research and grantmaking organization that tries to identify and make progress (when possible) on questions like these. Our interest in this kind of inquiry is driven not just by intellectual curiosity, but by altruism: we've found these questions to be very relevant to making the best grants we can. For us, this means maximizing the <a href='https://en.wikipedia.org/wiki/Expected_value' target='_blank' rel='noopener noreferrer'>expected</a> good done by each dollar we give away.
              </p>

              <p>
                This bookshelf contains some of the writings we've found most useful to our thinking about these and related questions. This includes books on nuclear and biological weapons, on the potential and risk of artificial intelligence, on global poverty, and on how to evaluate arguments in areas where your intuition offers little guidance.
              </p>

              <p>
                You might particularly enjoy these works if you see yourself as checking several of these boxes:
              </p>

              <ul>
                <li>Curious.</li>
                <li>Open-minded but skeptical.</li>
                <li>Interested in the impact of technology on a global scale.</li>
                <li>Interested in STEM, economics, politics, or philosophy.</li>
                <li>Interested in working hard to make the world a better place.</li>
              </ul>

              <p>
                We don't agree with every idea or argument in these books. But we think all the ideas are important and deserve to be more widely read and argued about.
              </p>
            </div>

            <h2>Why is it free? What are you getting out of this?</h2>
            <div>
              <p>
                There's no catch; we really do just want to provide free copies of these books to people who might be interested in them. We ask for your email at checkout, but we'll never sell it to anyone. We (<a href="https://www.openphilanthropy.org/" target="_blank" rel="noreferrer">Open Philanthropy</a>) are a grantmaking organization, not a for-profit company. We have a newsletter, but it's easy to opt out of at checkout time or later.
              </p>

              <p>
                Conflict of interest notice: one of the titles (<i>The Most Important Century</i>) was written by Open Philanthropy's co-CEO, Holden. He doesn't benefit financially from copies of his works that we're handing out here — the work in question is a series of blog posts that's free on the Internet anyway.
              </p>

              <p>
                Some of the other works were authored by grantees of ours, people who work at organizations that are grantees of ours, or people Open Philanthropy employees know socially. In some cases, they might stand to financially benefit from copies of their works distributed through this site.
              </p>
            </div>

            <h2>Who's behind this?</h2>
            <div>
              <p>
                As described above, this website was created and sponsored by Open Philanthropy, a research and grantmaking organization. Logistics and book delivery are being handled by EA Books Direct, a service run by the <a id='globalchallengesproject.org-link' href="https://www.globalchallengesproject.org/" target="_blank" rel="noreferrer noopener">Global Challenges Project</a>.
              </p>
            </div>
          </div>

          <FancySeparator/>

          <Links/>
        </div>
      </div>
    );
  }

  onClickGetAnotherBook = () => {
    // We want to scroll back to the top after exiting the default book display.
    this.setState({ displayingJustDefaultBook: false }, () => window.scrollTo(0, 0));
  }

  onBookAdded(format, newBook) {
    // Create a book object with the desired format.
    const newBookWithFormat = { ...newBook, format };

    // Put in the left slot only if that slot is empty; otherwise, we put it in the right slot (replacing whatever's there).
    if (this.state.leftBook) {
      this.setState({ rightBook: newBookWithFormat });
    } else {
      this.setState({ leftBook: newBookWithFormat });
    }

    this.closeModals();
  }

  onBookRemoved(toBeRemoved) {
    // Remove by filtering out the book, then sliding everything down by one.
    const afterRemoval = [this.state.leftBook, this.state.rightBook].filter((b) => b !== toBeRemoved);
    const afterSliding = afterRemoval.filter((e) => e !== undefined);

    this.setState({ leftBook: afterSliding[0], rightBook: afterSliding[1] });
    this.closeModals();
  }

  startDisplayingBookDetailsFor(book) {
    this.setState({ displayingBookDetailsFor: book });
  }

  closeModals() {
    this.stopDisplayingBookDetails();
  }

  stopDisplayingBookDetails() {
    this.setState({ displayingBookDetailsFor: undefined });
  }

  checkoutUrl() {
    const queryParams = {
      // Make the 'receive emails from us' box checked by default.
      'prefill_Opted into emails': '1',

      'prefill_Book #1': this.state.leftBook && this.state.leftBook.name,
      'prefill_Book #1 Format': this.state.leftBook && this.state.leftBook.format,

      'prefill_Book #2': this.state.rightBook && this.state.rightBook.name,
      'prefill_Book #2 Format': this.state.rightBook && this.state.rightBook.format,
    };

    const queryParamString = new URLSearchParams(queryParams).toString();
    return `https://airtable.com/shr4xC8rxFHbSBfu4?${queryParamString}`;
  }
}