  /**
   * Check the beginning of the line to make sure the footnotes don't get out of order.
   */
  renderFootnote(index, regex) {
    const textContent = Footnotes[index-1].props.children[0];
    if (!Footnotes[index-1] || !regex.test(textContent)) {
      throw new Error(`Footnote ${index} with text content "${textContent}..." does not match regex ${regex}`);
    }

    return (
      <sup>
        <a id={`footnote-${index}-source`} href="" onClick={(event) => onClickGoToId(event, `footnote-${index}-destination`)}>
          {index}
        </a>
      </sup>
    );
  }

  renderFootnotesSection() {
    const divs = Footnotes.map((element, index) => (
      <div key={index} style={{ marginTop: '1rem', fontSize: '10pt' }}>
        <sup id={`footnote-${index+1}-destination`}>
          {index+1}
        </sup>
        &nbsp;
        {element}
        &nbsp;
        <a href="" onClick={(event) => onClickGoToId(event, `footnote-${index+1}-source`)}>↩</a>
      </div>
    ));

    return <div style={{ marginTop: '5rem' }}>{divs}</div>;
  }
